.rb-wrapper{
  min-width: 320px;
  max-width: px2rem(750);
  margin: 0 auto;
}
.ui-header {
  height: px2rem(88);
  line-height: px2rem(88);
  position: relative;
  border-bottom:1px solid #ECEDEE;
  background: $color-white;

  h1{
    text-align: center;
    font-weight: normal;
    @include font-dpr(17px);
  }

  .ui-icon-return{
    background:  url("../img/back.png") no-repeat;
    display: block;
    width: px2rem(41);
    height: px2rem(41);
    position: absolute;
    left: px2rem(30);
    top: px2rem(20);
    cursor: pointer;
    -webkit-background-size: contain;
    background-size: contain;
  }
}

.main-nav{
  padding: px2rem(208) px2rem(59);
  ul{
    overflow: hidden;
  }
  li{
    float: left;
    text-align: center;
    margin: 0 px2rem(55) px2rem(70);
    a{
      display: block;
      color: #9b9b9b;
      @include font-dpr(17px)
    }
    i{
      display: block;
      width: px2rem(206);
      height: px2rem(203);
      margin-bottom:px2rem(24);
    }
    &:nth-child(1){
      i{
        background:  url("../img/setting_icon.png") no-repeat;
        -webkit-background-size: contain;
        background-size: contain;
      }
    }
    &:nth-child(2){
      i{
        background:  url("../img/playroobo_icon.png") no-repeat;
        -webkit-background-size: contain;
        background-size: contain;
      }
    }
    &:nth-child(3){
      i{
        background:  url("../img/playapp_icon.png") no-repeat;
        -webkit-background-size: contain;
        background-size: contain;
      }
    }
    &:nth-child(4){
      i{
        background:  url("../img/issue_icon.png") no-repeat;
        -webkit-background-size: contain;
        background-size: contain;
      }
    }
  }
}
.rb-content{
  margin-top: px2rem(30);
  border-top: 1px solid #F3F3F3;
  background: $color-white;
  overflow: hidden;

  .ui-selector-hd{
    &:last-of-type{
      border-bottom:none;
    }
  }

}


.ui-selector-hd{
  height: px2rem(100);
  line-height: px2rem(100);
  color: #9b9b9b;
  font-weight: normal;
  position: relative;
  padding-left: px2rem(62);
  cursor: pointer;
  margin: 0 px2rem(30);
  border-bottom: 1px #EBEBEB solid;
  @include font-dpr(17px);

  &:before{
    content: " ";
    position: absolute;
    display: block;
    overflow: hidden;
    width: px2rem(30);
    height: px2rem(17);
    background: url("../img/down.png") no-repeat;
    -webkit-background-size: contain;
    background-size:contain;
    top: px2rem(42);
    right: px2rem(30);
  }
  &.active{
    border-bottom:none;
    &:before{
      background: url("../img/up.png") no-repeat;
      -webkit-background-size: contain;
      background-size:contain;
    }
  }

  .msg-icon,
  .skill-icon,
  .request-icon,
  .act-icon,
  .voide-icon,
  .teach-icon,
  .volume-icon,
  .home-icon,
  .wifi-icon,
  .start-icon,
  .photo-icon,
  .binding-icon{
    height: px2rem(42);
    width:px2rem(42);
    overflow: hidden;
    display: block;
    position: absolute;
    left:0;
    top:px2rem(29);
  }
  .msg-icon{
    background: url("../img/msg_icon.png") no-repeat;
    -webkit-background-size:contain;
    background-size:contain;
  }
  .skill-icon{
    background: url("../img/skill_icon.png") no-repeat;
    -webkit-background-size:contain;
    background-size:contain;
  }
  .request-icon{
    background: url("../img/request_icon.png") no-repeat;
    -webkit-background-size:contain;
    background-size:contain;
  }
  .act-icon{
    background: url("../img/act_icon.png") no-repeat;
    -webkit-background-size:contain;
    background-size:contain;
  }
  .voide-icon{
    background: url("../img/voide_icon.png") no-repeat;
    -webkit-background-size:contain;
    background-size:contain;
  }
  .teach-icon{
    background: url("../img/teach_icon.png") no-repeat;
    -webkit-background-size:contain;
    background-size:contain;
  }
  .volume-icon{
    background: url("../img/volume_icon.png") no-repeat;
    -webkit-background-size:contain;
    background-size:contain;
  }
  .home-icon{
    background: url("../img/home_icon.png") no-repeat;
    -webkit-background-size:contain;
    background-size:contain;
  }
  .binding-icon{
    background: url("../img/binding_icon.png") no-repeat;
    -webkit-background-size:contain;
    background-size:contain;
  }
  .wifi-icon{
    background: url("../img/wifi_icon.png") no-repeat;
    -webkit-background-size:contain;
    background-size:contain;
  }
  .start-icon{
    background: url("../img/start_icon.png") no-repeat;
    -webkit-background-size:contain;
    background-size:contain;
  }
  .photo-icon{
    background: url("../img/photo_icon.png") no-repeat;
    -webkit-background-size:contain;
    background-size:contain;
  }


}
.ui-selector-bd{
  background: #F7F7F7;
  padding: px2rem(70) px2rem(90) px2rem(5);
  border-top:1px solid #EBEBEB;
  border-bottom:1px solid #EBEBEB;
  h4{
    color: #3f3e3f;
    font-weight:normal;
    position: relative;
    margin-bottom:px2rem(30);
    padding-left:px2rem(22);
    line-height: px2rem(42);
    @include font-dpr(14px);

    &:before{
      content: "";
      background: #7f7f7f;
      display: block;
      height: px2rem(12);
      width: px2rem(12);
      border-radius: px2rem(12);
      position: absolute;
      left:0;
      top: px2rem(15);
    }
  }
  p{
    color: #848484;
    margin-bottom:px2rem(70);
    @include font-dpr(13px);
    &.other-sty{
      color: #3f3e3f;
      margin-bottom:px2rem(50);
    }
  }
}

.conts-img{
  -webkit-display: flex;
  -moz-display: flex;
  -ms-display: flex;
  -o-display: flex;
  display: flex;
  display: -webkit-box;
  width: 100%;
  padding-bottom:px2rem(70);
  margin: 0 px2rem(-18);

  li{
    -webkit-box-flex: 1;
    width: 100%;
    padding: 0 px2rem(18);
    text-align: center;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-box-pack: center;
    -webkit-box-align: center;
  }
  &.img-two{
    width: 68.667%;
    margin: 0 auto;
  }
}

.rb-tel{
  line-height: px2rem(100);
  height:px2rem(100);
  background: $color-white;
  border-bottom: 1px #EBEBEB solid;
  border-top: 1px #EBEBEB solid;
  color: #878787;
  padding-left:px2rem(30);
  margin-top:px2rem(60);
  @include font-dpr(14px);

  span{
    display: inline-block;
    margin-left:px2rem(30);
  }
  a{
    color: #4A90E2;
  }
}
.ui-selector-bd{
  .img-box{
    //padding: 0 px2rem(30);
    margin-bottom:px2rem(30);
    text-align: center;
    &.img-s01{
      padding: 0 10% 0 30%;
      margin-bottom:px2rem(70);
    }
    &.img-s03{
      padding: 0 28%;
      margin-bottom:px2rem(70);
    }
  }
  .img-intro{
    text-align: center;
    color: #57c4ef;
    @include font-dpr(8px)
  }
}

.overlay{
  background: rgba(0,0,0,.8);
  height:100%;
  width: 100%;
  position: fixed;
  top:0;
  left:0;
  bottom: 0;
  display: none;
  text-align: center;
  img{
    position: absolute;
    width: 80%;
    left:50%;
    top:5%;
    margin-left:-40%;

  }
}




